import { Box, Center } from "@chakra-ui/react";
import React, { useState } from "react";

export default function SideScrollSelect(props) {	
	const init_scroll_offset = 50;
	const side_length = 80;
	const gap = 10;

	const [scroll_pos, set_scroll_pos] = useState(init_scroll_offset);

	const calc_focus_idx = (scroll_offset) => (scroll_offset - init_scroll_offset) / (side_length + gap);

	const round_idx = (idx, min, max) => Math.round(Math.min(Math.max(idx, min), max));

	const calc_dist_from_focus = (scroll_offset, idx) => Math.abs(calc_focus_idx(scroll_offset) - idx);

	const get_scroll_top = () => document.getElementById("sideScrollBox").scrollTop;

	const scroll_handler = () => {
		console.log(get_scroll_top());
		if (Math.abs(get_scroll_top() - scroll_pos) > 0) {
			set_scroll_pos(get_scroll_top());
			props.select_handler(round_idx(calc_focus_idx(get_scroll_top()), 0, props.categories.length));
		}
	}

	return (
		<Center w="100%" h={`${2 * init_scroll_offset}px`}>
			<Box 
				id="sideScrollBox"
				h={props.width ? `${props.width}vw` : "100vw"} 
				py={props.width ? `${props.width / 2}vw` : `calc(50vw - ${side_length / 2}px)`}
				transform="rotate(-90deg)" 
				overflowY="scroll" 
				sx={{
					"scrollSnapType": "y mandatory",
				}}
				onScroll={scroll_handler}
			>
				{props.categories.map((category, idx) => (
					<Center 
						transform="rotate(90deg)"
						w={`${side_length}px`} 
						h={`${side_length}px`} 
						m={`${gap}px`} 
						key={`category-${idx}`}
						sx={{"scrollSnapAlign": "center"}}
					>
						<Center 
							w="100%"
							h="100%"
							bg={idx === round_idx(calc_focus_idx(scroll_pos), 0, props.categories.length) 
								? `${category}.300` : "transparent"}
							color="#ffffff"
							transition="opacity .3s ease-out, transform .1s linear, background-color .3s ease-out"
							transform={`scale(${1 - .15 * calc_dist_from_focus(scroll_pos, idx)})`}
							opacity={`${1 - .4 * calc_dist_from_focus(scroll_pos, idx)}`}
							sx={{
								"clipPath": "polygon(0 50%, 50% 0, 100% 50%, 50% 100%)"
							}}
						>
							{category}
						</Center>
					</Center>
				))}
			</Box>
		</Center>
	);
}